import { s3Upload } from "aws";
import UploadFileArea from "components/UploadFileArea";
import * as CSV from "csv-string";
import jqueryCSV from "jquery-csv";
import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { getDBData, getFileName, getJobsList } from "redux/actions";
import { listDBRecords } from "redux/fetchers";
import { routePath } from "routing";

import { GRID_ID } from "../../constants";

function UploadPage(params) {
  const {} = params;

  const dispatch = useDispatch();
  let history = useHistory();

  const onDropAccepted = (file) => {
    window.localStorage.removeItem("grid" + GRID_ID);

    s3Upload(file).then((file_data) => {
      console.log(file_data);
      dispatch(getFileName(file_data.Key));
    });

    file.text().then((text) => {
      try {
        const separator = CSV.detect(text);

        let parsedCSV = jqueryCSV.toObjects(text, { separator });

        parsedCSV = parsedCSV.filter(
          (row) => Object.values(row).join("").length > 0
        );
        parsedCSV = parsedCSV.map((row) => {
          delete row[""];
          return row;
        });

        dispatch(getJobsList(parsedCSV));
        history.push(routePath.import);
      } catch (error) {
        console.log(error);
      }
    });
  };

  return (
    <Container>
      <Row>
        <Col>
          <UploadFileArea onDropAccepted={onDropAccepted} />
        </Col>
      </Row>
    </Container>
  );
}

export default UploadPage;
