import _ from "lodash";
import React, { useEffect } from "react";
//import { connect, useDispatch } from "react-redux";
import { Col, Container, Row, Spinner, Table } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { getDBData } from "redux/actions";
import { listDBRecords } from "redux/fetchers";

import MapBox from "../../components/MapBox/MapBox";

function GeocodePage(params) {
  const {} = params;
  const jobsList = useSelector((state) => state.jobList.db);
  const fileName = useSelector((state) => state.jobList.fileName);
  const dispatch = useDispatch();

  useEffect(() => {
    setInterval(() => {
      listDBRecords({ file_name: fileName }).then((data) => {
        const results = data.data.listServiceplannerRows.items.map((row) =>
          JSON.parse(row.data)
        );
        console.log(results);
        dispatch(getDBData(results));
      });
    }, 1500);
  }, []);

  return (
    <Container>
      <Row>
        <Col>
          {!jobsList.length && <Spinner animation="border" />}
          {!!jobsList.length && (
            <Table striped bordered hover>
              <thead>
                <tr>
                  {Object.keys(jobsList[0]).map((key) => {
                    if (key !== "geocode") {
                      return <td key={key}>{key}</td>;
                    }
                  })}
                  <td>geocode</td>
                </tr>
              </thead>
              <tbody>
                {jobsList.map((item, key) => {
                  return (
                    <tr
                      key={key}
                      className={item.geocode === "error" ? "error" : ""}
                    >
                      {_.map(item,(field, key) => {
                        if (key !== "geocode") {
                          return <td key={key}>{field}</td>;
                        }
                      })}
                      <td>
                        {item.geocode === undefined && (
                          <Spinner animation="border" size="sm" />
                        )}
                        {item.geocode !== undefined && item.geocode}
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </Table>
          )}
        </Col>
      </Row>
    </Container>
  );
}

export default GeocodePage;
